.home {
  display: flex;
  flex-flow: column;
  align-items: center;
  width: 80vw;
}
.home-links {
  display: flex;
  flex-flow: row;
}
.links-container {
  display: flex;
  flex-flow: column;
  margin: 1em;
  border-radius: 1em;
  border: 1px solid;
  padding: 0.5em;
}
.links-parent {
  align-self: center;
  font-weight: bold;
  font-size: 1.5em;
}
.links-children {
  font-weight: lighter;
  font-size: 1.3em;
}
.links-child {
  margin: 1em;
}
.home-image {
  width: 80%;
}
.home-image img {
  object-fit: contain;
  max-width: 100%;
  min-width: 100%;
}
